#root, html, body {
	margin: 0;
	padding: 0;
	height: 100vh;
	box-sizing: border-box;
}
	
.app {
	height: 100%;
	background: linear-gradient( rgba(9, 9, 121, 100),
				rgba(0, 212, 255, 100) );
	background-size: cover;
	background-position: center;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}
	
.card {
	background: whitesmoke;
	width: 40%;
	height: 30%;
	display: flex;
	align-items: center;
	flex-direction: column;
	border-radius: 25px;
	padding: 2%;
	box-shadow: 10px 10px;
}
	
.heading {
	display: flex;
	align-items: center;
	height: 350px;
	font-family: 'Spartan', sans-serif;
}
	
.button {
	position: relative;
	outline: none;
	text-decoration: none;
	border-radius: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	text-transform: uppercase;
	height: 200px;
	width: 210px;
	opacity: 1;
	background-color: #ffffff;
	border: 1px solid rgba(22, 76, 167, 0.6);
}
	
.button span {
	color: #164ca7;
	font-size: 12px;
	font-weight: 500;
	letter-spacing: 0.7px;
}
	
.button:hover {
	-webkit-animation: rotate 0.7s ease-in-out both;
	        animation: rotate 0.7s ease-in-out both;
}
	
.button:hover span {
	-webkit-animation: storm 0.7s ease-in-out both;
	        animation: storm 0.7s ease-in-out both;
	-webkit-animation-delay: 0.06s;
	        animation-delay: 0.06s;
}
	
@-webkit-keyframes rotate {
	0% {
	-webkit-transform: rotate(0deg) translate3d(0, 0, 0);
	        transform: rotate(0deg) translate3d(0, 0, 0);
	}
	25% {
	-webkit-transform: rotate(3deg) translate3d(0, 0, 0);
	        transform: rotate(3deg) translate3d(0, 0, 0);
	}
	50% {
	-webkit-transform: rotate(-3deg) translate3d(0, 0, 0);
	        transform: rotate(-3deg) translate3d(0, 0, 0);
	}
	75% {
	-webkit-transform: rotate(1deg) translate3d(0, 0, 0);
	        transform: rotate(1deg) translate3d(0, 0, 0);
	}
	100% {
	-webkit-transform: rotate(0deg) translate3d(0, 0, 0);
	        transform: rotate(0deg) translate3d(0, 0, 0);
	}
}
	
@keyframes rotate {
	0% {
	-webkit-transform: rotate(0deg) translate3d(0, 0, 0);
	        transform: rotate(0deg) translate3d(0, 0, 0);
	}
	25% {
	-webkit-transform: rotate(3deg) translate3d(0, 0, 0);
	        transform: rotate(3deg) translate3d(0, 0, 0);
	}
	50% {
	-webkit-transform: rotate(-3deg) translate3d(0, 0, 0);
	        transform: rotate(-3deg) translate3d(0, 0, 0);
	}
	75% {
	-webkit-transform: rotate(1deg) translate3d(0, 0, 0);
	        transform: rotate(1deg) translate3d(0, 0, 0);
	}
	100% {
	-webkit-transform: rotate(0deg) translate3d(0, 0, 0);
	        transform: rotate(0deg) translate3d(0, 0, 0);
	}
}
	
@-webkit-keyframes storm {
	0% {
	-webkit-transform: translate3d(0, 0, 0) translateZ(0);
	        transform: translate3d(0, 0, 0) translateZ(0);
	}
	25% {
	-webkit-transform: translate3d(4px, 0, 0) translateZ(0);
	        transform: translate3d(4px, 0, 0) translateZ(0);
	}
	50% {
	-webkit-transform: translate3d(-3px, 0, 0) translateZ(0);
	        transform: translate3d(-3px, 0, 0) translateZ(0);
	}
	75% {
	-webkit-transform: translate3d(2px, 0, 0) translateZ(0);
	        transform: translate3d(2px, 0, 0) translateZ(0);
	}
	100% {
	-webkit-transform: translate3d(0, 0, 0) translateZ(0);
	        transform: translate3d(0, 0, 0) translateZ(0);
	}
}
	
@keyframes storm {
	0% {
	-webkit-transform: translate3d(0, 0, 0) translateZ(0);
	        transform: translate3d(0, 0, 0) translateZ(0);
	}
	25% {
	-webkit-transform: translate3d(4px, 0, 0) translateZ(0);
	        transform: translate3d(4px, 0, 0) translateZ(0);
	}
	50% {
	-webkit-transform: translate3d(-3px, 0, 0) translateZ(0);
	        transform: translate3d(-3px, 0, 0) translateZ(0);
	}
	75% {
	-webkit-transform: translate3d(2px, 0, 0) translateZ(0);
	        transform: translate3d(2px, 0, 0) translateZ(0);
	}
	100% {
	-webkit-transform: translate3d(0, 0, 0) translateZ(0);
	        transform: translate3d(0, 0, 0) translateZ(0);
	}
}

@media only screen and (max-width: 1007px) {
	.card {
		width: 60%;
		height: 30%;
	}
}
	
@media only screen and (max-width: 600px) {
	.card {
		width: 80%;
		height: 40%;
	}
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

